@import "./nullstyle";
@import "./variable";
@import "./fonts";
@import "./mixins";

* {
  font-family: $font-family;
  line-height: 120%;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background: $contrast-color;
}

::-webkit-scrollbar-thumb {
  background: $primary-color;
}

html {
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  //background: $body-color;
  overflow-x: hidden;
  background: #333856 url(../img/bg-0.webp) center top/cover fixed no-repeat;
}

main {
  flex: 1 1 auto;
}

.container {
  max-width: 1370px;
  margin: 0 auto;
  width: 100%;
  padding: 0 10px;
}

.button {
  display: block;
  background: $primary-color;
  font-size: 15px;
  padding: 13px 0;
  color: $contrast-color;
  @include transition;

  &:hover {
    background: $primary-color-light;
  }
}

.title {
  font-weight: 700;
  color: $secondery-color;
  font-size: 32px;

  &_margin {
    margin-bottom: 28px;

  }
}

// HEADER
#header__logo {
  width: 161px;
  height: 48px;
  transform: scale(200%);
  fill: white;
  margin-left: 10px;
  user-select: none;
}

#footer__logo {
  width: 128px;
  height: 38px;
  transform: scale(200%);
}

#close-consultation {
  transition: fill 0.3s ease;
}

#close-consultation:hover {
  fill: $primary-color;
}

.header {
  margin-top: 40px;
  border-radius: 20px;
  background: $contrast-color;
  width: 100%;
  margin-bottom: 10px;

  &__top,
  &__bottom {
    padding: 20px 0;
    border-radius: 20px;
  }

  &__bottom {
    box-shadow: 0px 5px 15px rgb(0 0 0 / 11%);
    background: $contrast-color;

    .header__wrapper {
      justify-content: end;
    }
  }

  &__wrapper {
    @include flex(space-between);
    gap: 20px;
    max-width: 1370px;
    margin: 0 auto;
    width: 100%;
    padding: 0 10px;
  }

  &__search {
    max-width: 700px;
    width: 100%;
    display: flex;

    input {
      margin-left: 115px;
      width: 50%;
      font-family: $font-family;
      font-size: 16px;
      height: 40px;
      line-height: 20px;
      padding: 0 10px;
      box-shadow: none;
      border: 1px solid #ccc;
      border-right: none;
      border-radius: 20px 0 0 20px;

      /* добавляем скругления для левой стороны */
      &:focus {
        outline: none;

        &::placeholder {
          opacity: 0;
          transition: opacity 0.2s ease-in-out;
        }
      }

      &::-webkit-search-cancel-button {
        -webkit-appearance: none;
      }

      &::placeholder {
        opacity: 1;
        transition: opacity 0.2s ease-in-out;
      }
    }
  }

  &__btn {
    position: relative;
    font-size: 0;
    white-space: nowrap;
    fill: $primary-color;

    button {
      @include flex;
      background: none;
      height: 40px;
      width: 40px;
      border: 1px solid #ccc;
      border-left: none;
      border-radius: 0 20px 20px 0;
    }

    &.active-btn {
      border-color: #6c757d;
      box-shadow: -1px 0 5px rgba(108, 117, 125, 0.5);
      transition: border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    }
  }

  &__icons {
    margin-right: 50px;

    #userDiv {
      color: $primary-color;
      border-bottom: 1px solid black;
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      &:hover {
        color: black;
        border-bottom-color: $primary-color;
      }
    }

    max-width: 300px;
    width: 100%;
    @include flex(space-between);

    button {
      font-family: $font-family;
      font-size: 17px;
      color: $secondery-color;
      text-align: left;
      background: $contrast-color;
      display: flex;
      align-items: center;
      gap: 10px;

      &:hover {
        path {
          fill: $primary-color;
        }
      }
    }
  }

  &__basket {
    position: relative;
  }

  svg {
    path {
      @include transition(0.3s);
    }
  }

  .basket__dropdown {
    z-index: 20;
    position: absolute;
    min-width: 300px;
    background: $contrast-color;
    box-shadow: 0px 30px 60px rgb(0 0 0 / 36%);
    text-align: center;
    padding: 20px 0;
    bottom: -70px;
    right: 0;
    transform: scale(0);
    transform-origin: right;
    @include transition;

    &_active {
      transform: scale(1);
    }
  }

  // Header Bottom

  &__menu {
    width: 100%;
    @include transition;

    ul {
      width: 100%;
      @include flex(space-between);
      gap: 20px;

    }

    li {
      margin: 0 50px;

      &:last-child {
        a {

          border-radius: 20px 20px 20px 20px;
          background: $primary-color;
          padding: 8px 20px;
          color: $contrast-color;

          &::after {
            content: unset;
          }

          &:hover {
            background: $primary-color-light;
          }
        }
      }

    }

    a {
      font-family: $font-family;
      font-size: 17px;
      color: $secondery-color;
      position: relative;
      transition: all 0.3s ease-in-out;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;

        background: $primary-color;
        transform: scaleX(0);
        transform-origin: right;
        transition: transform 0.5s;
      }

      &:hover {
        color: $primary-color;

        &::after {
          transform: scaleX(1);
          transform-origin: left;
        }
      }
    }

  }

  .burger-btn {
    display: none;
    z-index: 10;
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;

    >span {
      position: absolute;
      background-color: $primary-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      top: 9px;
      @include transition;
    }

    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: $primary-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      @include transition;
    }

    &:before {
      top: 20px;
    }

    &:after {
      bottom: 20px;
    }

    &_active {
      top: 20px;
      right: 20px;
      position: fixed;
      z-index: 10002;

      span {
        opacity: 0;
        background-color: $contrast-color;
      }

      &:before {
        transform: rotate(45deg);
        top: 9px;
        background-color: $contrast-color;
      }

      &:after {
        transform: rotate(-45deg);
        bottom: 9px;
        background-color: $contrast-color;
      }
    }
  }

  @media screen and(max-width:768px) {
    &__logo {
      width: 100px;
      position: relative;
      left: -25px;
    }

    &__wrapper {
      gap: 0;
      padding: 0;
    }

    &__search {
      display: none !important;
    }

    &__sign {
      margin-left: 10px;
      margin-right: 10px;
    }

    &__icons {
      justify-content: end;
      margin-right: 10px;
    }

    .burger-btn {
      display: block;
    }

    &__bottom {
      padding-right: 10px;
    }

    &__bottom:last-child {
      //display: none;
    }

    &__menu {
      position: absolute;
      max-width: none;
      flex-direction: column;
      gap: 30px;
      z-index: 2;
      right: -100%;
      top: 0;
      width: 80%;
      height: 100%;
      padding: 70px 50px;
      background: $primary-color;
      transform: scaleX(0);
      transform-origin: right;
      transition: all 0.2s ease;

      ul {
        @include flex($flex-direction: column);
      }

      a {
        font-size: 50px;
        color: $contrast-color;
        align-items: center;
      }

      &_active {
        transform: scaleX(1);
        right: 0;
        position: fixed;
        width: 100%;
        z-index: 10001;

      }
    }

  }

  @media screen and(max-width:400px) {
    &__basket {
      p {
        display: none;
      }
    }
  }
}

// POP-UP

.popup-consultation {
  position: fixed;
  width: 100%;
  height: 100%;
  top: -300%;
  left: 0;
  z-index: 10;
  background: rgba(0, 0, 0, 0.6);
  @include transition(1s);

  &__body {
    min-height: 100%;
    display: flex;
    align-items: center;
    margin: 0 auto;
    max-width: 460px;
    width: 100%;
  }

  &__content {
    position: relative;
    padding: 40px;
    width: 100%;
    background: $contrast-color;
    border-radius: 20px;
  }

  &__top {
    @include flex(space-between);
    width: 100%;
    margin-bottom: 30px;
  }

  &__title {
    font-family: $secondery-color;
    font-size: 26px;
    color: $secondery-color;

  }

  &__close {
    cursor: pointer;
  }

  form {
    @include flex(space-between, $flex-wrap: wrap);
    width: 100%;
  }

  .contact__input {
    flex-basis: 100%;

  }

  &__input {
    display: flex;
    flex-direction: column-reverse;
    position: relative;
    flex-basis: 100%;
    height: 65px;
    margin-bottom: 30px;

  }

  input,
  textarea {
    width: 100%;
    font-family: $font-family;
    font-size: 12px;
    color: $secondery-color;
    padding: 10px 5px;
    border: 0;
    border: 1px solid $secondary-color-light;
    border-radius: 2px;
    border-radius: 20px 20px 20px 20px;
    transition: all 0.3s ease;

    &:focus {
      padding: 15px 10px;
      border: 1px solid $primary-color;
    }
  }

  textarea {
    height: 70%;
  }

  label {
    position: absolute;
    left: 0;
    top: 0;
    font-family: $font-family;
    font-size: 12px;
    color: $secondery-color;
  }

  .button {
    max-width: 320px;
    margin: 0 auto;
    font-family: $font-family;
    font-size: 17px;
    font-weight: 500;
    color: $primary-color;
    background: $contrast-color;
    padding: 16px 36px;
    border: 1px solid $primary-color;
    cursor: pointer;
    @include transition;

    &:hover {
      background: $primary-color;
      color: $contrast-color;
    }
  }

  p {
    width: 100%;
  }

  @media screen and (max-width: 768px) {
    &__body {
      height: 100%;
    }

    &__content {
      height: 100%;
      overflow: scroll;
    }

    &__input {
      flex-basis: 100%;
    }
  }
}

.tabs {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  min-height: 300px; //only for demo porpuses

  .t-btn-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 60px;
    align-items: center;

    .t-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      &.active {
        color: $primary-color;
      }
    }
  }

  .t-panel-container {
    .t-panel {
      transition: all 0.2s ease-in-out;
      opacity: 0;
      height: auto;
      max-height: 0px;
      overflow: hidden;

      &.active {
        opacity: 1;
        height: auto;
        max-height: 500px;
      }
    }
  }
}

// BREADCRUMB

.breadcrumb {
  padding: 8px 15px;
  margin-bottom: 28px;
  display: flex;

  a {
    font-size: 12px;
    font-weight: 400;
    color: $contrast-color;
  }

  strong {
    font-size: 12px;
    font-weight: 500;
    color: $primary-color-light;
  }
}


// HOME-CATALOG

.cat-name {
  font-size: 18px;
}

.cat-checkbox {
  width: 25px;
  //border-radius: 30%;
}

.home-cat {
  &__img {
    width: 700px;

    &:first-child {
      width: 300px;
      height: 350px;
    }
  }

  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 10px;
  margin-bottom: 30px;
  /*&__wrapper{
    width: 200px;
  height: 200px;
  }
  &__img{
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }*/

  &__item {
    img {
      width: 500px;
      height: 350px;
      object-fit: contain;
    }

    @include flex(space-between);
    background: $contrast-color;
    padding: 35px 20px 30px 40px;
    grid-column: span 1 / span 1;
    border-radius: 20px 20px 20px 20px;
    @include transition;

    &:first-child {
      padding: 75px 120px;
      grid-column: span 2 / span 2;

      .home-cat__description {
        width: 35%;
      }

      .home-cat__title {
        font-size: 48px;
        margin-bottom: 40px;

        &:hover {
          color: $primary-color;
        }
      }

      .home-cat__sub-title {
        font-size: 28px;
        text-transform: uppercase;
      }

      .button {
        max-width: 217px;
        width: 100%;
        border-radius: 20px 20px 20px 20px;
      }

      &:hover {
        .home-cat__title {
          color: $secondery-color;
        }
      }
    }

    &:hover {
      transform: scale(1.1);
      box-shadow: 0px 0px 100px -40px rgba(0, 0, 0, 1);

      .home-cat__title {
        color: $primary-color;
      }
    }
  }


  &__description {
    width: 57%;
  }

  &__sub-title {
    font-size: 14px;
    margin-bottom: 30px;
    color: $secondery-color;
  }

  &__title {
    font-size: 26px;
    text-transform: uppercase;
    color: $secondery-color;
    margin-bottom: 10px;
    @include transition;
  }

  .button {
    @include flex;
    max-width: 182px;
    width: 100%;
    border-radius: 20px 20px 20px 20px;

  }

  @media screen and (max-width: 1024px) {
    &__item {
      padding: 30px 10px;
    }
  }

  @media screen and (max-width: 768px) {
    grid-template-columns: repeat(1, minmax(0, 1fr));

    &__item {
      grid-column: span 2 / span 2;
      flex-direction: column;

      img {
        width: 200px;
        height: 200px;
      }

      &:first-child {
        padding: 35px 20px 30px 40px;
        flex-direction: column-reverse;

        .home-cat__description {
          width: 100%;
        }

        .home-cat__title {
          font-size: 30px;
        }
      }
    }

    &__description {
      width: 100%;
      @include flex($flex-direction: column);
    }

    &__img {
      margin-bottom: 30px;
      width: 200px;
    }

    .preview {}
  }
}

// HOME-INFO

.home-info {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 10px;
  margin-bottom: 80px;

  &__item {
    display: flex;
    align-items: center;
    gap: 20px;
    background: $contrast-color;
    padding: 25px 20px;
  }

  &__description {
    svg {
      flex-shrink: 0;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 16px;
    color: $primary-color;
  }

  &__text {
    font-weight: 14;
    color: $secondery-color;
  }

  @media screen and(max-width:1024px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  @media screen and(max-width:768px) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

// HOME-PRODUCT

.home-product {
  width: 100%;
  position: relative;

  .title {
    text-align: center;
    color: white;
  }

  .swiper-button-disabled {
    svg {
      opacity: 0.25;
    }
  }

  &__prev,
  &__next {
    @include flex;
    position: absolute;
    top: 45%;
    width: 48px;
    height: 48px;
    background: $contrast-color !important;
    box-shadow: 0px 0px 10px rgb(0 0 0 / 15%);
    border-radius: 50%;
    z-index: 9;
    outline: none !important;
    border: none !important;
    cursor: pointer;

    svg {
      @include transition;

      path {
        @include transition;
      }
    }

    &:hover {
      svg {
        path {
          fill: $primary-color;
        }
      }
    }
  }

  &__prev {
    left: -25px;
  }

  &__next {
    right: -25px;
  }

  &__item {
    background-color: $contrast-color;
    max-width: 340px;
    width: 100%;
    padding: 30px;
    border-radius: 20px 20px 20px 20px;
    min-height: 515px;
  }

  &__img {
    max-width: 268px;
    width: 100%;
    height: 200px;
    margin-bottom: 43px;
    object-fit: contain;
    user-select: none;
  }

  &__name {
    margin-bottom: 10px;

    a {
      font-size: 16px;
      font-weight: 600;
      color: $secondery-color;
    }
  }

  &__table {
    width: 100%;
    margin-bottom: 20px;

    table {
      width: 100%;
    }

    td {
      margin-bottom: 2px;

      &:first-child,
      &:last-child {
        font-size: 14px;
        color: $secondary-color-light;
        white-space: nowrap;
        width: 50%;
        display: block;
        float: left;
        text-overflow: ellipsis;
        //overflow: hidden;
      }

      &:last-child {
        color: $secondery-color;
      }
    }
  }

  &__price-block {
    display: flex;
    flex-direction: column;
  }

  &__price {
    display: flex;
    flex-flow: wrap;
    font-size: 18px;
    font-weight: 500;
    width: 60%;
    text-align: left;
    margin-bottom: 20px;

    &-old {
      color: $secondery-color;
      font-weight: 500;
      font-size: 14px;
      display: block;
      text-decoration: line-through;
      float: left;
      width: 100%;
      margin-bottom: 6px;
    }

    &-new {
      font-weight: 600;
      font-size: 24px;
      color: $primary-color;
      float: left;
    }
  }

  &__save {
    color: $secondery-color;
    flex-flow: wrap;
    font-size: 14px;
    text-align: right;
    font-weight: 500;
    width: 40%;
    padding-top: 3px;
  }

  &__percent {
    width: 100%;
  }

  &__buttons {
    display: flex;
    gap: 20px;
  }

  &__button {
    border: 1px solid $primary-color;
    color: $secondery-color;
    background: $contrast-color;
    border-radius: 20px 20px 20px 20px;

    @include transition(0.3s);

    &:hover {
      background: $primary-color;
      color: $contrast-color;
      border-color: $primary-color;
    }
  }

  .button {
    max-width: 48%;
    height: 48px;
    width: 100%;
    padding: 0;
    @include flex;
    padding: 10px 5px;
  }

  @media screen and(max-width:1400px) {
    &__prev {
      left: -10px;
    }

    &__next {
      right: -10px;
    }
  }
}

// DEVICES
.title_margin {
  color: white;
}

.devices {
  @include flex(space-evenly, $flex-wrap: wrap);
  padding: 28px;
  background: $contrast-color;
  border-radius: 20px 20px 20px 20px;

  &__item {
    width: 16.66666667%;
    margin-bottom: 40px;

    a {
      padding: 15px;
      text-align: center;
      border-radius: 20px 20px 20px 20px;
      @include flex($flex-direction: column);
      @include transition;

      &:hover {
        box-shadow: 0px 10px 50px -20px rgba(0, 0, 0, 1);
        border-radius: 20px 20px 20px 20px;

        .devices__name {
          color: $primary-color;
        }
      }
    }
  }

  &__item:nth-child(5) a img,
  &__item:nth-child(10) a img,
  &__item:nth-child(11) a img,
  &__item:nth-child(12) a img {
    object-fit: cover;
  }

  &__img {
    width: 100%;
    width: 156px;
    height: 156px;
    object-fit: contain;
    margin-bottom: 10px;
  }

  &__name {
    display: block;
    font-size: 14px;
    line-height: 16px;
    font-weight: 500;
    color: $secondery-color;
    @include transition;
  }

  @media screen and(max-width:1200px) {
    &__item {
      width: 33.33333333%;
    }
  }

  @media screen and(max-width:600px) {
    &__item {
      width: 50%;
    }
  }

  @media screen and(max-width:420px) {
    &__item {
      width: 100%;
    }
  }
}

// CONTACT

.contact {
  border-radius: 20px 20px 20px 20px;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  width: 100%;
  padding: 40px;
  background: $contrast-color;

  &__title {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 15px;
    color: $secondery-color;
  }

  &__text {
    font-size: 14px;
    margin-bottom: 15px;
    color: $secondery-color;
    font-weight: 400;
  }

  span {
    a {
      color: #2980b9;
    }
  }
}

// CONTACT-FORM

.contact-form {
  margin-top: 15px;
  background-color: $contrast-color;
  border-radius: 20px 20px 20px 20px;

  &__container {
    max-width: 1370px;
    margin: 0 auto;
    width: 100%;
    padding: 40px;


  }

  &__header {
    font-size: 20px;
    font-weight: 100;
  }

  &__text {
    margin-top: 12px;
    font-size: 14px;
    font-weight: 100;
  }

  &__input {
    border: 1px solid #777e90;
    border-radius: 20px 20px 20px 20px;
    font-size: 12px;
    color: #292b31;
    padding: 10px 5px;
    width: 350px;
    margin-bottom: 15px;
    color: $secondary-color-light;
    background-color: $contrast-color;
    cursor: pointer;
    resize: none;
    &:last-child {
      background-color: $primary-color;
      border: 1px solid $contrast-color;
      color: $contrast-color;
      @include transition;

      &:hover {
        background-color: $contrast-color;
        border: 1px solid $primary-color;
        color: $primary-color;
      }
    }
  }

  @media screen and(max-width:640px) {
    &__input {
      width: 100%;
    }
  }
}

// CATALOG

.catalog {
  width: 100%;
  display: flex;
  gap: 10px;

  &__left {
    background: $contrast-color;
    width: 25%;
    padding: 0 10px;
    border-radius: 20px 20px 20px 20px;

  }

  &__right {
    width: 75%;
  }

  &__top {
    @include flex(space-between);
    width: 100%;
    padding: 20px 0;

    h2 {
      margin-bottom: 0;
    }
  }

  &__border-bottom {
    border-bottom: 1px solid $border-color;

  }

  &__title {
    font-size: 20px;
    color: $secondery-color;
    margin-bottom: 10px;
  }

  &__cancled {
    padding: 1px 6px;
    background: #fff;
    font-size: 14px;
    color: $secondery-color;
    font-weight: 400;
    border-radius: 12px;
    border: 1px solid $primary-color;
  }

  .price-change-filter {
    display: flex;
    gap: 20px;
    width: 85%;
    margin-bottom: 20px;

    input {
      border-radius: 20px 20px 20px 20px;
      width: 48%;
      float: left;
      text-align: center;
    }
  }

  .input-sm {
    height: 30px;
    padding: 5px 10px;
    font-size: 12px;
    line-height: 1.5;
    border-radius: 3px;
  }

  .form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
    box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
    -webkit-transition: border-color ease-in-out 0.15s,
      -webkit-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow;
  }

  &__budget {
    @include flex($flex-direction: column);
    padding: 10px 8px 10px 15px;

    h4 {
      margin-bottom: 5px;
    }
  }

  &__spoiler {
    padding: 10px 8px;
  }

  &__option {
    @include flex(space-between);
    margin-bottom: 10px;
  }

  &__sub-title {
    color: $secondery-color;
    font-size: 14px;
    font-weight: 500;
  }

  &__svg {
    transform: rotate(90deg);
    @include transition;

    &_active {
      transform: rotate(-90deg);
    }
  }

  &__cont {
    max-height: 1000px;
    overflow: hidden;
    @include transition;

    &_active {
      max-height: 20px;
    }
  }

  &__desc {
    a {
      color: #292b31 !important;
      border: none !important;
      padding-left: 0 !important;
      padding-right: 0 !important;
      background: none !important;
    }

    label {
      position: relative;
      cursor: pointer;
      display: block;
      font-size: 12px;
      font-weight: normal;
      line-height: 1.5;
      white-space: normal;
      padding: 4px 0 4px 30px;
      width: 100%;
      text-decoration: none;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;

      &::before {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        position: absolute;
        background: #fff;
        left: 0;
        top: 3px;
        border-radius: 3px;
        border: 1px solid #9ca0ab;
      }
    }
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .apply_filter {
    border-radius: 20px 20px 20px 20px;
  }

  // CATALOG-RIGHT

  #catalogTitle {
    color: white;
  }

  &__text {
    padding: 20px;
    font-size: 14px;
    color: $secondery-color;
    background: #fff;
    margin-bottom: 8px;
    border-radius: 20px 20px 20px 20px;

  }

  &__sort {
    background: $contrast-color;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 16px;
    margin-bottom: 8px;
    border-radius: 20px 20px 20px 20px;

    span {
      color: $secondery-color;
      margin-right: 15px;
      font-size: 14px;
      font-weight: 500;
    }

    button {

      font-size: 14px;
      text-align: center;
      color: $secondery-color;
      padding: 8px;
      background: none;
      border: 1px solid $border-color;
      border-radius: 20px 20px 20px 20px;

      @include transition;
    }

    &_active:hover {
      border-color: $primary-color !important;
      color: $contrast-color !important;
      background: $primary-color !important;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 10px;
  }

  &__item {
    background-color: $contrast-color;
    max-width: 340px;
    width: 100%;
    padding: 30px;
    cursor: pointer;
    @include transition;
    border-radius: 20px 20px 20px 20px;

    &:hover {
      box-shadow: 0 10px 25px rgb(0 0 0 / 11%);

      .catalog__img {
        transform: scale(1.1);
      }
    }
  }

  &__img {
    max-width: 268px;
    width: 100%;
    max-height: 268px;
    margin-bottom: 43px;
    @include transition;
    object-fit: contain;
    max-height: 120px;
  }

  &__name {
    margin-bottom: 10px;

    a {
      font-size: 16px;
      font-weight: 600;
      color: $secondery-color;
    }
  }

  &__table {
    width: 100%;
    margin-bottom: 20px;

    table {
      width: 100%;
    }

    td {
      margin-bottom: 2px;

      &:first-child,
      &:last-child {
        font-size: 14px;
        color: $secondary-color-light;
        white-space: nowrap;
        width: 50%;
        display: block;
        float: left;
        text-overflow: ellipsis;
        //overflow: hidden;
      }

      &:last-child {
        color: $secondery-color;
      }
    }
  }

  &__price-block {
    display: flex;
    flex-direction: column;

  }

  &__price {
    display: flex;
    flex-flow: wrap;
    font-size: 18px;
    font-weight: 500;
    width: 60%;
    text-align: left;
    margin-bottom: 20px;

    &-old {
      color: $secondery-color;
      font-weight: 500;
      font-size: 14px;
      display: block;
      text-decoration: line-through;
      float: left;
      width: 100%;
      margin-bottom: 6px;
    }

    &-new {
      font-weight: 600;
      font-size: 24px;
      color: $primary-color;
      float: left;
    }
  }

  &__save {
    color: $secondery-color;
    flex-flow: wrap;
    font-size: 14px;
    text-align: right;
    font-weight: 500;
    width: 40%;
    padding-top: 3px;
  }

  &__percent {
    width: 100%;
  }

  &__buttons {
    display: flex;
    gap: 20px;
  }

  &__button {
    border: 1px solid $primary-color;
    color: $secondery-color;
    background: $contrast-color;
    @include transition;
    border-radius: 20px 20px 20px 20px;

    &:hover {
      background: $primary-color;
      color: $contrast-color;
      border-color: $primary-color;
    }
  }

  .button {
    max-width: 48%;
    height: 48px;
    width: 100%;
    padding: 0;
    @include flex;
  }

  @media screen and(max-width:1024px) {
    &__content {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }

  @media screen and(max-width:768px) {
    &__left {
      //display: none;
    }

    &__right {
      width: 100%;
    }
  }

  @media screen and(max-width:550px) {
    &__sort {
      flex-wrap: wrap;
    }
    &__text{
      display: none;
    }

    &__content {
      grid-template-columns: repeat(1, minmax(0, 1fr));
    }
  }
}

.apply_filter {
  margin-top: 10px;
  display: flex;
  margin: 15px auto;
}

// PRODUCT
.purchase {
  border-radius: 20px 20px 20px 20px;
}

.product {
  &__content {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    background: $contrast-color;
    padding: 20px 12px 44px 48px;
    margin-bottom: 10px;
    gap: 40px;
    border-radius: 20px 20px 20px 20px;
  }

  &__gallery {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__big {
    width: 367px;
    height: 367px;
    object-fit: contain;

    &:first-child {
      flex-basis: 100%;
    }
  }

  &__small {
    max-width: 48px;
    height: 48px;
    width: 100%;
    object-fit: contain;
  }

  &__description {
    padding-left: 50px;
  }

  &__title {
    font-weight: 500;
    font-size: 28px;
    color: $secondery-color;
    margin-bottom: 10px;
  }

  &__in-stock {
    background: #e5fbeb;
    width: 95px;
    text-align: center;
    color: #199e3f;
    font-weight: 500;
    margin-bottom: 20px;
    margin-top: 20px;
    padding: 2px 0;
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    color: $secondery-color;
    margin-bottom: 10px;
  }

  &__attr-item {
    td:first-child {
      font-size: 14px;
      color: $secondary-color-light;
      font-weight: 400;
      padding: 6px 0px;
      width: 100%;
    }

    td:last-child {
      padding: 5px 0px;
      padding-left: 25px;
      color: $secondery-color;
      font-size: 14px;
      width: 100%;
    }
  }

  &__buy {
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0px 8px 40px rgb(0 0 0 / 16%);
    width: 100%;
    height: 35%;
