    margin-bottom: 4px;
  }

  &__special {
    font-weight: bold;
    font-size: 24px;
    color: $primary-color;
  }

  &__informer {
    width: 100%;
    font-size: 11px;
    font-weight: 500;
  }

  &__right {
    width: 46%;
    padding-left: 36px;
    text-align: right;
    max-height: 64px;
  }

  &__code {
    margin-bottom: 6px;
    font-size: 14px;
    color: $secondery-color;
    font-weight: 500;
  }

  &__buy-block {
    width: 100%;
  }

  &__buttons {
    margin-bottom: 15px;
  }

  &__card {
    width: 100%;
    margin-bottom: 15px;
  }

  &__order {
    width: 100%;
    height: 48px;
    background: $contrast-color;
    color: $secondery-color;
    border: 1px solid $secondery-color;
    font-weight: 500;
    @include transition;

    &:hover {
      color: #199e3f;
      border-color: #199e3f;
    }
  }

  &__info {
    ul {
      margin-bottom: 10px;
    }
  }

  &__icons {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__nav {
    width: 100%;
    background: $contrast-color;
    margin-bottom: 10px;

    span {
      font-size: 16px;
      font-weight: 500;
      color: $primary-color;
      text-transform: uppercase;
      line-height: 60px;
      padding: 0 20px;
    }
  }

  &__char {
    padding: 20px;
    background: $contrast-color;
    border-radius: 20px 20px 20px 20px;
  }

  &__table {
    .strong {
      font-weight: 700;
      color: $secondery-color;
      padding: 15px 0px;
      font-size: 16px;
      background: $contrast-color;
    }

    tr {
      &:nth-child(odd) {
        background: rgba(196, 196, 196, 0.2);
      }
    }

    td {
      padding: 15px 10px;
      font-size: 14px;

      &:first-child {
        border-radius: 20px 0 0 20px;
      }

      &:last-child {
        border-radius: 0 20px 20px 0;

      }
    }
  }

  svg {
    .a {
      fill: #fff;
      stroke: #d83939;
      stroke-width: 2px;
    }

    .b {
      fill: #d83939;
    }

    .c {
      stroke: none;
    }

    .d {
      fill: none;
    }
  }

  @media screen and(max-width:1024px) {
    &__content {
      padding: 20px 20px 40px 20px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    &__buy {
      grid-column: span 2 / span 2;
    }
  }

  @media screen and(max-width:670px) {
    &__content {
      grid-template-columns: repeat(1, minmax(0, 1fr));
    }

    &__buy,
    &__gallery,
    &__description {
      grid-column: span 1 / span 1;
    }

    &__description {
      padding-left: 0;
    }

    &__big {
      text-align: center;
    }
  }

  @media screen and(max-width:400px) {
    &__big {
      width: 100%;
      max-height: 250px;
    }
  }
}

// FOOTER

.footer {
  margin-bottom: 30px;
  border-radius: 20px;
  background: $footer-color;
  margin-top: 100px;
  padding: 30px 0;

  &__wrapper {
    max-width: 1370px;
    margin: 0 auto;
    width: 100%;
    padding: 0 40px;
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
  }

  &__title {
    font-size: 18px;
    color: $secondery-color;
    margin: 10px 0;
    text-transform: uppercase;
  }

  &__block {
    display: flex;
    flex-direction: column;
    align-items: baseline;
  }

  &__text {
    display: inline-block;
    color: $secondery-color;
    font-size: 14px;
    padding: 6px 0;
    position: relative;
    @include transition(0.3s);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: $primary-color;
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 0.5s;
    }

    &:hover {
      color: $primary-color;

      &::after {
        transform: scaleX(1);
        transform-origin: left;
      }
    }
  }

  &__social {
    margin-top: 10px;
    display: flex;
    gap: 10px;
  }

  .button {
    border-radius: 20px 20px 20px 20px;
    margin-top: 6px;
    padding: 10px 15px;
  }

  &__pow {
    text-align: center;
    display: inline-block;
    color: #ccc;
    font-size: 14px;
    padding: 6px 0;
    margin-top: 45px;
    grid-column: span 5 / span 5;
  }

  @media screen and(max-width:1024px) {
    &__wrapper {
      grid-template-columns: repeat(4, minmax(0, 1fr));
      gap: 20px;
    }

    &__block {
      align-items: center;
      justify-content: center;
      text-align: center;
      grid-column: span 2 / span 2;
    }

    &__last-block {
      grid-column: span 4 / span 4;
    }

    &__pow {
      padding-top: 25px;
    }
  }

  @media screen and(max-width:500px) {
    &__block {
      grid-column: span 4 / span 4;
    }
  }
}



